@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;1,300&display=swap');


.App {
  text-align: center;
  font-family: "Poppins";
  color: #2D334A;
}

.App-logo {
  height: 10vmin;
  pointer-events: none;
}



.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

.App-link {
  background-color: #FFD803;
  font-family: "Poppins";
  font-weight: bold;
  border-radius: 7px;
  padding: 10px 60px;
  font-size: 1rem;
  border-style: none;
  margin: 7px;
  text-decoration: none;
  color: #2D334A;
}

.nav{
  margin: 0;  
  padding: 5px;
  -webkit-box-shadow: 0px 0.1px 55px -30px rgb(241, 126, 126);
  -moz-box-shadow: 0px 0.1px 55px  -30px rgba(0,0,0,1);
  box-shadow: 0px 0.1px 55px  -30px rgba(0,0,0,1)!important;
}


.logo{
  width: 150px;
}